export default {
	state:{
		list:[
			{
				name:"",
				tel:"18511773322",
				city:"",
				details:'2',
				isDefault:false
			},
			{
				name:"",
				tel:"18511773322",
				city:"",
				details:'2',
				isDefault:true  //
			}
		]
	},
	getters:{
		defaultPath(state){
			return state.list.filter(v=>v.isDefault)
		}
	},
	mutations:{
		createPath( state, obj ){
			state.list.unshift( obj );
		},
		updatePath(state,{index,item}){
			for( let key in item){
				state.list[index][key] = item[key];
			}
		},
		//
		removePath(state){
			state.list.forEach(v=>{
				if(v.isDefault){
					v.isDefault = false;
				}
			})
		}
	},
	actions:{
		createPathFn({commit},obj){
			if(obj.isDefault){
				commit("removePath");
			}
			commit('createPath',obj);
		},
		updatePathFn({commit},obj){
			if( obj.item.isDefault ){
				commit("removePath");
			}
			commit('updatePath',obj);
		}
	}
}